@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: linear-gradient(rgba(0,0,0,0.3 ), transparent), url(/deelle/static/media/isa3.5725126a.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.text-box {
  position: absolute;
  color: #fff;
  top: 25%;
  left: 16%;
}

.text-box h1 {
  font-size: 120px;
  line-height: 100px;
  font-family: FFF Tusj;
  color: transparent;
  -webkit-text-stroke: 1px #fff;
  background: url(data:image/png;base64,);
  background-clip: text;
  -webkit-background-clip: text;
  background-position: 0 0;
  -webkit-animation: back 20s linear infinite;
          animation: back 20s linear infinite;
}
@-webkit-keyframes back{
  100%{
    background-position: 2000px 0;
  }
}
@keyframes back{
  100%{
    background-position: 2000px 0;
  }
}


.social-menu ul {
  position: absolute;
  top: 30%;
  left: 28%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
}

.social-menu ul .fa{
  position: relative;
  font-size: 30px;
  line-height: 60px;
  transition: .6s;
  color: #fff;
}

.social-menu ul .fab{
  position: relative;
  padding: 10;
  margin: 10;
  font-size: 30px;
  line-height: 60px;
  transition: .6s;
  color: #fff;
}

.social-menu ul a{
  position: relative;
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #000;
  text-align: center;
  transition: .6s;
  /*box-shadow: 0 5px 4px rgba(0,0,0,.5);*/
}

.social-menu ul .fa:hover{
  color: none;
}

.social-menu ul .fab:hover{
  color: none;
}

.social-menu ul a:hover{
  -webkit-transform: translate(0, -10px);
          transform: translate(0, -10px);
}

.social-menu ul a:hover:nth-child(1){
  background-color: #E4405F;
}

.social-menu ul a:hover:nth-child(2){
  background-color: #1877F2;
}

.social-menu ul a:hover:nth-child(3){
  background-color: #EE1D51;
}

.social-menu ul a:hover:nth-child(4){
  background-color: #1DA1F2;
}

.social-menu ul a:hover:nth-child(5){
  background-color: #CD201F;
}

.each-slide {
  position: fixed;
  align-content: center;
  top: 12%;
  left: 33%;
  display: block;
}

.each-slide > div {
  position: relative;
  display: flex;
  align-content: center;
  justify-content: center;
  background-size: cover;
  border: whitesmoke;
  border-style: double;
  border-radius: 20px;
  height: 500px;
  width: 500px;
}

.block {
  position: absolute;
  top: 10%;
  left: 18%;
  display: flex;
}

Form {
  position: absolute;
  top: 45%;
  left: 18%;
  display: block;
}

Form.Item {
  position: relative;
  top: 30%;
  margin: 30%;
  padding-bottom: 30%;

}

.alinea { 
  text-indent: 20px;
  margin: 3%;
}

.home, 
.order, 
.photos,
.about,
.contact {
  display: block;
  height: 1vh;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: whitesmoke;
  padding-left: 40vh;
}

.frame {
  display: block;
  padding-top: 5vh;
  padding-left: 40vh;
}


.navbar {
    /* background-color: #060b26; */
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.menu-bars {
    margin-left: 1rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: black;
    width: 200px;
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 30px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 195px;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 60%;
}

.navbar-toggle {
    background-color: black;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

span {
    margin-left: 1px;
    padding: 0 10px;
}

